.nav-container {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    transition: all 0.2s;
    z-index: 99;
}

.nav-container.navblack {
    background-color: #111;
}

.image {
    height: 80px;
    padding-left: 1rem;
}

.image1 {
    padding-top: 18px;
    height: 50px;
    padding-right: 1rem;
}

.banner-head {
    object-fit: contain;
    color: white;
    height: 448px;
}

.banner-props {
    margin-left: 3rem;
    padding-top: 140px;
    height: 190px;
}    

.banner-title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner-btn {
    border: none;
    outline: none;
    padding: 0.5rem 1.4rem;
    color: white;
    margin-left: 1rem;
    font-weight: 700;
    cursor: pointer;
    border-radius: 2px;
    background-color: rgba(34, 33, 33, 0.68);
    transition: all .5s;
}

.banner-btn:hover {
    color: black;
    background-color: white;
}

.darkbottom {
    height: 7.4rem;
    background-image: linear-gradient(
        180deg, 
        transparent,
        #111
    );
}


.description {
    line-height: 1.3;
    height: 80px;
    width: 380px;    
    font-size: 1rem;
    padding-top: 1rem;
}

@media only screen and (max-width: 628px) {
    
    .banner-props {
        margin-left: 1.7rem;
    }    
    .banner-title {
        font-size: 2rem;
        padding-bottom: 0.2rem;
    }

    .description {
        width: 280px;   
    }
    
}