.img-container {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}

.img-container::-webkit-scrollbar {
  display: none;
}

.subtitle {
  color: white;
}

.row {
  padding-left: 20px;
}

.row-img {
  width: 100%;
  object-fit: contain;
  max-height: 100px;
  margin-right: 1rem;
  transition: 0.5s;
}

.row-img:hover {
  transform: scale(1.08);
}

.row-largeimg {
  max-height: 250px;
}
